<script>
  import exploreAPI from '@/api/exploreAPI'
  import Util from '@/common/Util.js'
  export default{
    name: '',
    data () {
      return {
        isData: false,
        searchFor: '',
        searchLoad: true,
        showPage: false,
        searchInfo: [],
        country: [],
        industry: [],
        valueCountry: '全部国家/地区',
        valueIndustry: '全部行业',
        countryVal: '',
        industryVal: '',
        page: 1,
        pre: true,
        next: false,
        number: 9,
        totalPage: -1,
        userId: window.localStorage.userId
      }
    },
    computed: {
      searchPage () {
        this.page = this.$store.getters.searchPage
        return this.$store.getters.searchPage
      }
    },
    components: { },
    mounted () {
      this.searchFor = this.$route.query.searchText
      let params = {keywords: this.searchFor, page: this.searchPage, pageSize: 9, region: this.countryVal, industry: this.industryVal}
      this.infoResult(params)
    },
    watch: {
//      监听页数的变化
      page () {
        if (this.page === 1) {
          console.log(8888)
          this.pre = true
        } else {
          this.pre = false
        }
        if (this.page === this.totalPage) {
          this.next = true
        } else {
          this.next = false
        }
      }
    },
    methods: {
//      搜索接口
      handClickIcon () {
        this.page = 1
        this.$store.commit('DEVELOP_SEARCH_PAGE', {page: this.page})
        window.location.hash = 'searchResult?searchText=' + this.searchFor
        let params = {keywords: this.searchFor, page: 1, pageSize: 9, region: this.countryVal, industry: this.industryVal}
        this.infoResult(params)
      },
      infoResult (val) {
        this.searchLoad = true
        exploreAPI.getSearchResult(val).then(res => {
          this.searchLoad = false
          if (res.data.code === 0) {
            for (let j = 0; j < res.data.data.records.length; j++) {
              console.log(88)
              for (let x = 0; x < res.data.data.records[j].includeList.length; x++) {
                if (res.data.data.records[j].includeList[x].url !== null && res.data.data.records[j].includeList[x].url !== '') {
                  res.data.data.records[j].includeList[x].url = Util.checkUrl(res.data.data.records[j].includeList[x].url)
                }
              }
            }
            this.searchInfo = res.data.data.records
            this.searchInfo.forEach((obj) => {
              if (obj.intro.length > 150) {
                obj.intro = obj.intro.substr(0, 150) + '...'
              }
            })
            this.totalPage = res.data.data.totalPages
//              页数是否大于2页
//            if (this.totalPage < 2) {
//              this.pre = true
//              this.next = true
//            }
            if (res.data.data.records.length > 0) {
              this.isData = false
              this.showPage = true
            } else {
              this.isData = true
              this.showPage = false
            }
          } else {
            this.$message.warning(res.data.message)
          }
        })
      },
//      国家的值发生改变
      changeCountry (val) {
        this.countryVal = val
        if (val === undefined) this.countryVal = ''
        this.page = 1
        let obj = {keywords: this.searchFor, page: this.page, pageSize: 9, region: this.countryVal, industry: this.industryVal}
        this.infoResult(obj)
      },
      changeIndustry (val) {
        this.page = 1
        this.industryVal = val
        if (val === undefined) this.industryVal = ''
        let obj = {keywords: this.searchFor, page: this.page, pageSize: 9, region: this.countryVal, industry: this.industryVal}
        this.infoResult(obj)
      },
//      添加探索公司
      addCompany (item) {
        for (let i = 0; i < item.thirdInfoList.length; i++) {
          if (item.thirdInfoList[i].homepageUrl !== null && item.thirdInfoList[i].homepageUrl !== '') {
            item.thirdInfoList[i].homepageUrl = Util.checkUrl(item.thirdInfoList[i].homepageUrl)
          }
        }
        if (item.website !== null && item.website !== '') {
          item.website = Util.checkUrl(item.website)
        }
        let params = {
          type: 1,
          userId: this.userId,
          name: item.name,
          avatar: item.avatar,
          mail: item.mail,
          telephone: item.telephone,
          address: JSON.stringify(item.address),
          thirdAccountId: item.id,
          createSource: 'recommend_search_create',
          keywords: this.searchFor,
          remark: item.intro,
          socialAccountType: 6,
          industry: item.industry,
          website: item.website,
          thirdInfoList: JSON.stringify(item.thirdInfoList),
          companySize: item.companySize
        }
        exploreAPI.addContact(params).then(res => {
          if (!res.data.code) {
            this.$store.commit('FIND_COMPANYID', {companyId: res.data.data.data.id})
            item.added = true
            this.$message.success('添加成功')
          } else {
            this.$message.error(res.data.message)
          }
        })
      },
//      跳转公司详情
      jumpCompany (id) {
        this.$store.commit('DEVELOP_SEARCH_PAGE', {page: this.page})
        this.$router.push(
          {
            path: '/companyDetail',
            query: {id: id, source: 'search', keywords: this.searchFor}
          }
        )
      },
//      跳转到联系人公司详情
      showCompany (id) {
        this.$router.push({
          path: '/contactDetail',
          query: {
            contactId: this.$store.getters.companyId || id,
            contactType: 1
          }
        })
      },
//      上一页
      prePage () {
        this.page--
        let obj = {keywords: this.searchFor, page: this.page, pageSize: 9, region: this.countryVal, industry: this.industryVal}
        this.infoResult(obj)
      },
//      下一页
      nextPage () {
        this.page++
        let obj = {keywords: this.searchFor, page: this.page, pageSize: 9, region: this.countryVal, industry: this.industryVal}
        this.infoResult(obj)
      }
    },
    created () {
//      国家、地区列表
      exploreAPI.getCountry({dictCode: 'COUNTRY_REGION'}).then(res => {
        this.country = res.data.data
//        this.country.unshift({dictdataName: '全部国家/地区', id: -1})
        this.country.unshift({dictdataName: '全部国家/地区'})
        console.log(this.country)
      })
//      行业列表
      exploreAPI.getCountry({dictCode: 'INDUSTRY'}).then(res => {
        this.industry = res.data.data
//        this.industry.unshift({dictdataName: '全部行业', id: -1})
        this.industry.unshift({dictdataName: '全部行业'})
      })
    }
  }
</script>

